<script>
import axios from 'axios';

export default {
    name: 'ParcelCreation',

    
    data: () => ({
        selectedCategory: '',
        officeDatabase: [],
        //
        service: '',
        parcel: '',
        weight: '',
        weightEnabled: false,
        //
        // senderOffice: '',
        // recipientOffice: '',
        senderCity: '',
        senderAddress: '',
        recipientCity: '',
        recipientAddress: '',

        //
        parcelAlert: false,
        parcelPriceRecieved: false,
        parcelPrice: 0,
        parcelIntro: true,
        parcelDetails: false,

        //
        typeOfService: ['  ', '  ', '  ', '  '],
        typeOfParcel: ['', ''],
        // typeWeight: [' 1', ' 1.001  2', ' 2.001  3', ' 3.001  4',
        // ' 4.001  5', ' 5.001  6', ' 6.001  7', ' 7.001  8', 
        // ' 8.001  9', ' 9.001  10', ' 10.001  11', ' 11.001  12',],
        evropatOffices: [{}],
    }),

    computed: {
        officeService() {
            if(this.service=='  ') {
                return true
            } else {
                return false
            }
        },
        officeAddressService() {
            if(this.service=='  ') {
                return true
            } else {
                return false
            }
        },
        addressOfficeService() {
            if(this.service=='  ') {
                return true
            } else {
                return false
            }
        },
        addressService() {
            if(this.service=='  ') {
                return true
            } else {
                return false
            }
        },
    },

    methods: {
        documentPackage() {
            if(this.parcel=='') {
                this.weight = '0.5'
                this.weightEnabled = true
            } else {
                this.weightEnabled = false
                this.weight = '0'
            }
        },

        async getOffices() {
            let payload = await axios.request('/parcel/get-offices', payload)
            let evropatOffices = []
            
            this.officeDatabase = payload.data[0].offices
            this.officeDatabase.forEach(function (item) {
                // console.log(item.officeName, index)
                evropatOffices.push(item.officeName)
            });
            evropatOffices.sort()
            this.evropatOffices = evropatOffices
        },

        async checkPrice() {

            let indexService = this.typeOfService.findIndex(object => {
                return object === this.service;
            });

            let indexType = this.typeOfParcel.findIndex(object => {
                return object === this.parcel;
            });

            this.destinationType = this.selectedCategory
            
            let indexSendAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.senderAddress;
            });

            let indexRespAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.recipientAddress;
            });

            let payload = {
                'parcelType': indexType,
                'parcelWeight': this.weight,
                'destinationType': this.destinationType,
                'deliveryType': indexService,
                'senderAddress': this.officeDatabase[indexSendAddress].officeDestinationID,
                'recipientAddress': this.officeDatabase[indexRespAddress].officeDestinationID,
            }
            // console.log('Payload', payload)
            let request = await axios.post('/parcel/check-price', payload)
            console.log('Response', request)
            let parcelHolder = request.data[0].response[1].mainServicePrice
            this.parcelPrice = parcelHolder
            this.parcelPriceRecieved = true

            // if(request.data[0].status == 'success'){
            //     this.isRegister = false;
            //     this.errorMessage = "";
            // }  else if(request.data[0].status == 'emailExists') {
            //     this.errorMessage = "Email already exists!";
            // }
        },

        async sendParcel() {
            if(this.password == this.confirmPassword){

                let payload = {
                    'email': this.email,
                    'password': this.password,
                    'first_name': this.firstName,
                    'last_name': this.lastName,
                    'phone_number': this.phone_number
                }
                // console.log('Payload', payload)
                let request = await axios.post('/register', payload)
                // console.log('Response', request)

                if(request.data[0].status == 'success'){
                    this.isRegister = false;
                    this.errorMessage = "";
                }  else if(request.data[0].status == 'emailExists') {
                    this.errorMessage = "Email already exists!";
                }
                
            }
            else {
                this.errorMessage = "Passwords do not match!"
            }
        },

        proceedDetails() {
            this.parcelIntro = false
            this.parcelDetails = true
        }
    },

    mounted() {
        this.getOffices()
    },
};
</script>

